<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="search-box">
        <div class="item">
          <span class="text">学期</span>
          <el-select @change="getDetail()" class="term-input" v-model="query.termId">
            <el-option v-for="item in termList" :key="item.schoolTermId"
                       :label="item.termName" :value="item.schoolTermId"
            ></el-option>
          </el-select>
        </div>
        <div class="item">
          <span class="text">年级</span>
          <el-select @change="getDetail()" v-model="query.gradeId">
            <el-option v-for="item in gradeList" :key="item.id"
                       :label="item.name" :value="item.id"
            ></el-option>
          </el-select>
        </div>
        <div class="item">
          <span class="text">学科</span>
          <el-select @change="getDetail()" v-model="query.courseId">
            <el-option v-for="item in courseList" :key="item.id"
                       :label="item.name" :value="item.id"
            ></el-option>
          </el-select>
        </div>
      </div>
      <resize-table class="mt20">
      <table class="table">
        <thead>
        <tr>
          <th>年级</th>
          <th>班级</th>
          <th>教师姓名</th>
          <th>对比入口差值</th>
          <th>对比目标差值</th>
          <th>班级平均</th>
          <th>等级</th>
        </tr>
        </thead>
        <tr v-for="(item,key) in detail.data">
          <td>{{item.gradeName}}</td>
          <td>{{item.className}}</td>
          <td>{{item.teacherName}}</td>
          <td>{{item.entranceANumDiff}}</td>
          <td>{{item.targetANumDiff}}</td>
          <td>{{item.avgANum}}</td>
          <td>
            <span :class="['assess', item.assessLevel]">{{$enums.AssessLevel.getName(item.assessLevel) || '--'}}</span>
          </td>
        </tr>
      </table>
      </resize-table>
      <no-data v-show="detail.data && detail.data.length < 1"></no-data>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "教学成绩", path: ""}],
        query:{
          termId: null,
          gradeId: null,
          courseId: null
        },
        // 学期列表
        termList: [],
        // 年级列表
        gradeList: [],
        // 学科列表
        courseList: [],
        detail: [],
      }
    },
    methods: {
      // 获取学期
      async getTermList(){
        let res = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.termList = res
        this.termList.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.query.termId = e.schoolTermId
          }
        })
      },
      // 获取年级列表
      async getGradeList() {
        let res = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
        this.gradeList = res
        this.query.gradeId = res[0].id
      },
      // 获取学科
      async subject(){
        let res = await this.$ajax({
          url: '/teachGroupCourse/queryCourseList.do'
        })
        this.courseList = res
        this.query.courseId = this.courseList[0].id
      },
      //
      async getDetail(){
        this.detail = await this.$ajax({
          url: '/score/queryTeachScoreList.do',
          query: this.query
        })
      }
    },
    async mounted(){
      let p1 = this.getTermList()
      let p2 = this.getGradeList()
      let p3 = this.subject()
      let vm = this
      Promise.all([p1, p2, p3]).then(function (results) {
        vm.getDetail()
      });

    }
  }
</script>
<style lang="sass" scoped>
  .index
    .search-box
      margin-top: 10px
      .term-input
        width: 240px
      .item
        display: inline-block
        margin-right: 15px
      .text
        margin-left: 5px
        color: $main-c
        display: block
        margin-bottom: 3px
    .assess
      display: inline-block
      width: 24px
      height: 16px
      line-height: 16px
      text-align: center
      font-size: $font-small
      color: #fff
      border-radius: 2px
    .LEVEL_A
      background: $success-c
    .LEVEL_B
      background: $main-c
    .LEVEL_C
      background: $warning
</style>
